.out-leave {
  display: flex;
  border-radius: 8px; 
  width: 307px; 
  height: 34px; 
  background-color: #ffffff;
  border: 1px solid #9acefb;
  justify-content: space-between;
  align-items: center;
  padding-left: 10px;
}

.out-enter {
  display: flex;
  border-radius: 25px; 
  border-width: 5px;
  border-color: #444444;
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.3); 
  width: 350px; 
  height: 50px; 
  background-color: #ffffff;
  justify-content: space-between;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}

.split {
  width: 1px;
  height: 20px;
  background-color: #aaaaaa;
}

.search-icon {
  width: 20px;
  height: 20px;
}

.search-icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 34px;
  background-color: #0086f6;
  border-radius: 0px 8px 8px 0px;
}