.top-chart-container {
  display: flex;
  width: 80%;
  height: 60px;
  min-width: 900px;
  border: 1px solid #cccccc;
  margin-bottom: 20px;
}

.top-chart-item {
  display: flex;
  flex-direction: column;
  padding: 5px 10px;
  border-right: 1px solid #cccccc;
  justify-content: space-between;
}

.top-chart-label {
  font-size: 14px;
  color: #8592a6;
  
}

.top-chart-content {
  font-weight: 700;
  font-size: 16px;
  color: #0f294d !important;
}

.top-chart-icon {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  background-color: #0086f6;
}