.top-container {
  display: flex;
  width: 80%;
  height: 72px;
  justify-content: space-between;
  align-items: center;
  min-width: 900px;
}

.logo-searchbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 525px;
  height: 40px;
}

.logo {
  width: 194px;
  height: 32px;
  cursor: pointer;
}

.topbar-right-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 411px;
  height: 40px;
}

.topbar-right-font {
  font-size: 14px;
  color: #666;
}

.vertical-line {
  width: 1px;
  height: 20px;
  background-color: #dadfe6;
}