.container {
  display: flex;
  width: 600px;
  height: 278px;
  border-radius: 80px;
}

.left-container {
  width: 380px;
  margin-right: 10px;
  display: flex;
  justify-content: center;
}

.grid-container {
  width: 240px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 4px;
}

.grid-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 85px;
}