.main-card-container {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin-bottom: 8px;
  border-radius: 8px;
  background-color: #ffffff;
  min-width: 1000px;
}

.main-card-top-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  height: 82px;
  flex-grow: 1;
  margin: 16px;
}

.main-card-bottom-container {
  display: flex;
  justify-content: space-between;
  height: 278px;
  flex-grow: 1;
  margin: 16px;
}

.main-card-detial-container {
  display: flex;
  height: 278px;
  width: 470px;
  flex-direction: column;
  justify-content: space-between;
}

.score {
  background-color: #4978ce;
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 22px;
  line-height: 22px;
  color: hsla(0, 0%, 100%, 0.913);
  font-weight: 700;
  white-space: nowrap;
}

.main-card-good {
  color: #4978ce;
  font-size: 20px;
  margin-left: 8px;
  font-weight: 700;
  position: relative;
  top: 1px;
}

.main-card-showall {
  font-size: 14px;
  color: #0066f6;
  font-weight: 700;
  cursor: pointer;
  white-space: nowrap;
}

.main-card-good1 {
  margin-bottom: 4px;
  font-size: 14px;
  color: #06875a;
  font-weight: 400;
  white-space: nowrap;
}

.main-card-geo {
  word-break: break-all;
  line-height: 18px;
  font-size: 14px;
  color: #455873;
  letter-spacing: 0;
  background-color: #e3e3e3;
  padding: 5px;
  border-radius: 5px;
}

.main-card-traffic-distance {
  font-size: 14px;
  color: #0f294d;
  letter-spacing: 0;
  text-align: left;
  line-height: 18px;
  margin-left: 4px;
  cursor: pointer;
  border-bottom: 1px dotted #0f294d;
}

.main-card-detial-row {
  display: flex;
  width: 100%;
  padding-bottom: 10px;
}

.select-room-btn {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 8px 16px;
  background: #0066f6;
  border-radius: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
}

.main-card-title {
  font-size: 20px;
  color: #0f294d;
  display: inline;
  margin-right: 8px;
  font-weight: 600;
}

.main-title-container {
  display: flex;
  align-items: end;
  margin-bottom: 8px;
}

.main-info-container {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.main-card-horizon-line {
  width: 100%;
  height: 1px;
  background-color: #eeeeee;
}