.user-container-leave {
  display: flex;
  width: 100px;
  height: 50px;
  border-radius: 25px;
  /* box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);  */
  border: 1px solid #888888;
  cursor: pointer;
}

.user-container-enter {
  display: flex;
  width: 100px;
  height: 50px;
  border-radius: 25px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3); 
  border: 1px solid #888888;
  cursor: pointer;
}

.user-icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 50px;
}

.user-icon {
  height: 40px;
  width: 40px;
}

.menu-icon {
  height: 20px;
  width: 20px;
}